import React from 'react'
import { Link } from 'react-router-dom';
import "../App.css"
export default function LandingPage() {
  return (
    <div className='landingPageContainer'>
      <nav>
        <div className='navHeader'>
            <h2><b>PhotoWaliCall</b></h2>
        </div>
        <div className='navlist'>
            <p>Join as Guest</p>
            <p>Register</p>
            {/* <button>Login</button> */}
            <div role='button'><p>Login</p></div>
        </div>
      </nav>

      <div className='landingMainContainer'>
        <div>
            <h1><span style={{color:"#ff9800"}}>Connect</span> With Loved One</h1>

            <p>Cover a distance by Photo wali Call</p>
            <div role='button'>
                <Link to="/auth"><p>Get Started</p></Link>
            </div>

        </div>

        <div>
            <img src="/mobile.png" alt='loading'/> 
        </div>

      </div>
      
    </div>
  )
}
